<template>
    <div>
        <div class="image-preview">
            <div class="checkerboard-background">
                <img v-if="imageUrl" :src="imageUrl" alt="이미지 미리보기" @load="getImageResolution" />
            </div>
            <div v-if="resolution" class="resolution-info">
            {{ resolution }}
        </div>
        </div>
        
    </div>  
</template>
  
<script>
export default {
  props: {
    imageUrl: String,
  },
  data() {
    return {
      resolution: null,
    };
  },
  methods: {
    getImageResolution(event) {
      const img = event.target;
      this.resolution = `${img.naturalWidth} x ${img.naturalHeight}`;
    },
  },
};
</script>

<style scoped>
.image-preview {
    position: relative;
    display: inline-flex;
    align-items: flex-start; /* 상단 정렬 */
}
.checkerboard-background {
    position: relative;
    background-image:
    linear-gradient(45deg, #ccc 25%, transparent 25%, transparent 75%, #ccc 75%),
    linear-gradient(45deg, #ccc 25%, transparent 25%, transparent 75%, #ccc 75%);
    background-size: 20px 20px;
    background-position: 0 0, 10px 10px;
}

.image-preview img {
    border: 2px solid black;
    position: relative;
    z-index: 1;
}

.resolution-info {
    
    background-color: rgba(0, 0, 0, 0.3); /* 검은색 배경, 투명도 30% */
    color: white; /* 텍스트 색상 흰색 */
    padding: 5px;
    border-radius: 5px;
    margin-left: 10px;
}
</style>