<template>
    <div>
        <div v-if="loading">변환 중...</div>
        <div v-else-if="convertedData">
            <button @click="downloadFile">다운로드</button>
        </div>
    </div>
</template>
  
  <script>
  import FileSaver from "file-saver";
  
  export default {
    props: {
        convertedData: [String, Uint8Array],
        outputType: String,
        loading: Boolean,
    },
    methods: {
        downloadTextFile() {
            const blob = new Blob([this.convertedData], { type: "text/plain;charset=utf-8" });
            FileSaver.saveAs(blob, "converted.txt");
        },
        downloadBinFile() {
            const blob = new Blob([this.convertedData], { type: "application/octet-stream" });
            FileSaver.saveAs(blob, "converted.bin");
        },
        downloadFile() {        
            if(this.outputType == "bin") {
                this.downloadBinFile();
            } else {
                this.downloadTextFile();
            }
        
        },
    },
  };
  </script>
