<template>
    <div>
      <label for="output-format">출력 형식:</label>
      <select id="output-format" v-model="outputFormat">
        <option value="RGBA8888">RGBA8888</option>        
        <option value="RGBA5551">RGBA5551</option>
        <option value="RGBA4444">RGBA4444</option>
        <option value="ARGB8888">ARGB8888</option>
        <option value="YUV420">YUV420</option>
        <option value="YUV422">YUV422</option>
        <option value="UYVY">UYVY</option> 
      </select>
  
      <label for="output-type">출력 파일 형식:</label>
      <select id="output-type" v-model="outputType">
        <option value="c-array">C Array</option>
        <option value="bin">BIN</option>
      </select>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        outputFormat: "RGBA8888",
        outputType: "c-array",
      };
    },
    watch: {
      outputFormat(newValue) {
        this.$emit("update:outputFormat", newValue);
      },
      outputType(newValue) {
        this.$emit("update:outputType", newValue);
      },
    },
  };
  </script>
