<template>
  <div>
    <!-- <img src="@/assets/logo_novatool2.png" alt="NovaTool 로고" /> -->
    <div class="logo-img"></div>
    <FileUpload @file-selected="handleFileSelected" />

    <ImagePreview :imageUrl="imageUrl" />
    <ConversionOptions
      v-model:outputFormat="outputFormat"
      v-model:outputType="outputType"
    />
    <button @click="convertImage">변환</button>
    <ConversionResult
      :convertedData="convertedData"
      :outputType="outputType"
    />
  </div>
</template>

<script>
import FileUpload from "./components/FileUpload.vue";
import ImagePreview from "./components/ImagePreview.vue";
import ConversionOptions from "./components/ConversionOptions.vue";
import ConversionResult from "./components/ConversionResult.vue";

export default {
  components: {
    FileUpload,
    ImagePreview,
    ConversionOptions,
    ConversionResult,
  },
  data() {
    return {
      file: null,
      imageUrl: null,
      outputFormat: "RGBA8888",
      outputType: "c-array",
      convertedData: null,
      loading: false,
    };
  },
  methods: {
    handleFileSelected(file) {
      this.file = file;
      this.imageUrl = URL.createObjectURL(file);
    },
    async convertImage() {
      if (!this.file) return;

      this.loading = true; // 변환 시작 시 로딩 상태를 true로 설정
      this.convertedData = null; // 기존 데이터 초기화

      const reader = new FileReader();
      reader.onload = (event) => {
        const img = new Image();
        img.onload = () => {
          const canvas = document.createElement("canvas");
          canvas.width = img.width;
          canvas.height = img.height;
          const ctx = canvas.getContext("2d");
          ctx.drawImage(img, 0, 0);
          const imageData = ctx.getImageData(0, 0, img.width, img.height).data;

          let binData = null;
          let binLength = 0;
          switch(this.outputFormat)
            {
              case "RGBA8888":
              case "ARGB8888":
                binLength = imageData.length;                
                break;

              case "RGBA5551":
              case "RGBA4444":
              binLength = imageData.length / 2;    
                break;            
          }         

          binData = new Uint8Array(binLength);

          let numsofrow = 16;
          let rgba4444 = 0;
          let rgba5551 = 0;
          let index = 0;
          for (let i = 0; i < imageData.length; i+=4) {
            let red = imageData[i];
            let green = imageData[i + 1];
            let blue = imageData[i + 2];
            let alpha = imageData[i + 3];

            switch(this.outputFormat)
            {
              case "RGBA8888":
                binData[i + 0] = imageData[i + 0];
                binData[i + 1] = imageData[i + 1];
                binData[i + 2] = imageData[i + 2];
                binData[i + 3] = imageData[i + 3];
                break;

              case "ARGB8888":
                binData[i + 0] = imageData[i + 3];
                binData[i + 1] = imageData[i + 0];
                binData[i + 2] = imageData[i + 1];
                binData[i + 3] = imageData[i + 2];                                
                break; 

              case "RGBA5551":
                red = imageData[i + 0] >> 3;
                green = imageData[i + 1] >> 3;
                blue = imageData[i + 2]  >> 3;
                alpha =imageData[i + 3]  > 0 ? 1 : 0;

                rgba5551 = (alpha << 15) | (blue << 10) | (green << 5) | red;
                binData[index++] = (rgba5551 >> 8) & 0xff;
                binData[index++] = rgba5551 & 0xff;
                break;

              case "RGBA4444":
                red = imageData[i + 0] >> 4;
                green = imageData[i + 1] >> 4;
                blue = imageData[i + 2]  >> 4;
                alpha =imageData[i + 3]  >> 4; 

                rgba4444 = (alpha << 12) | (blue << 8) | (green << 4) | red;
                binData[index++] = (rgba4444 >> 8) & 0xff;
                binData[index++] = rgba4444 & 0xff;
                break;

              case "YUV420":
                break;
              case "YUV422":
                break;
              case "UYVY":
                break;
            }
          }

          if(this.outputType == "bin")
          {
            this.convertedData = binData;
          }
          else
          {          
            let cArray = "/*\n";
            cArray += `  File Name: ${this.file.name}\n`;
            cArray += `  Width: ${img.width}\n`;
            cArray += `  Height: ${img.height}\n`;
            cArray += `  Array Length: ${binLength}\n`;
            cArray += `  Array type: ${this.outputFormat}\n`;
            cArray += "*/\n";
            cArray += "unsigned char image[] = {\n";

            for (let i = 0; i < binData.length; i++) {
              cArray += "0x" + binData[i].toString(16).padStart(2, "0") + ", ";              
              if ((i + 1) % numsofrow === 0) {
                cArray += "\n";
              }              
            }
            cArray += "\n};";
            this.convertedData = cArray;
          }
          this.loading = false; // 변환 완료 후 로딩 상태를 false로 설정
        };
        img.src = event.target.result;
      };
      reader.readAsDataURL(this.file);
    },
  },
};

</script>

