<template>
    <div @dragover.prevent @drop="handleDrop" class="drop-area">
      <input type="file" @change="handleFileChange" ref="fileInput" style="display: none" />
      <button @click="$refs.fileInput.click()">파일 선택</button>
      <p>또는 파일을 여기로 드래그 앤 드롭하세요.</p>
    </div>
  </template>
  
  <script>
  export default {
    methods: {
      handleFileChange(event) {
        this.$emit("file-selected", event.target.files[0]);
      },
      handleDrop(event) {
        this.$emit("file-selected", event.dataTransfer.files[0]);
      },
    },
  };
  </script>
  
  <style scoped>
  .drop-area {
    border: 2px dashed #ccc;
    padding: 20px;
    text-align: center;
    cursor: pointer;
  }
  </style>
  